import React from 'react'
import { connect } from 'react-redux'
import ActionButtons from '../../../components/ActionButtons'
import Searchbar from '../../../components/Searchbar'
import Table from '../../../components/Table'
import TitleViews from '../../../components/TitleViews'
import { getProductList } from '../../../redux/actions/ProductsAction'


const mapPropsToState = (state) => {
    return{
        products : state.Products.list
    }
}

const mapDispatchToProps = (dispatch) => {
    return{
        getProducts : () => dispatch(getProductList())
    }
}

const ManageProducts = connect(mapPropsToState, mapDispatchToProps)(({products, ...actions}) => {
    const columns = [
        {
            field: 'category',
            headerName: 'Categoria',
            editable: false,
            flex : 1,
            renderCell : (params) => {
                // console.log(params.value.name)
                return(
                    <>{params.value.name}</>
                )
            },
        },{
            field: 'name',
            headerName: 'Nombre',
            editable: false,
            flex : 1,
        },{
            field: 'skucode',
            headerName: 'Codigo SKU',
            editable: false,
            flex : 1,
        },{
            field: 'stock',
            headerName: 'Cantidad',
            editable: false,
            flex : 1,
            renderCell : (params) => {
                // console.log(params.row.stock)
                return(
                    <>{params.row.stock} {params.row.unitSize.abbreviation}</>
                )
            }
        },{
            field: ' ',
            headerName: ' ',
            renderCell : (params) => {
                return <ActionButtons
                view={false}
                disabled={false}
            />
            },
            flex : 1,
        },
    ]
    const { getProducts } = actions

    React.useEffect(() => {
        getProducts()
    },[])

    return(
        <>
            <TitleViews
                title="Lista de productos"
                tooltip="Crear nuevo producto"
                mb={2}
                // onClick={() => goCreateCategory()}
                // icon={<CategoriesSVG />}
            />
            <Searchbar />
            <Table
                rows={products}
                columns={columns}
                rowCount={10}
                updatePage={10}
                page={1}
                loading={true}
            />
        </>
    )
})

export default ManageProducts