import React from 'react';
import Dialog from './Dialog';
import AlertMUI from '@material-ui/lab/Alert';
import { Box } from '@material-ui/core';
import info from '../handlers/localeText'

const Alert = ({isOpen, title='Mensaje del sistema', component, severity="error", alert, locale, prop, close=() => null}) => {
    return(
        <>
            <Dialog component={component} title={title} open={isOpen} close={close} alert={ info(alert) } >
                <Box mb={1} >
                    <AlertMUI severity={severity}>
                        { info(locale, prop) }
                    </AlertMUI>
                </Box>
            </Dialog>
        </>
    )
}

export default Alert