const locate = {
    DELETE_CATEGORY : (category_name) => `Desea eliminar la categoria ${category_name}?`,
    DELETE_CATEGORY_ALERT : `Esta categoria tiene productos asignados, al ser eliminada los productos pasaran a estado "no disponible" hasta que sean asignados a otras categorias`,
    LIMIT_PRICES_ALER : (quantity) => `Solo puede aadir hasta ${quantity} precios`,
}
export default function info(text, param){
    return typeof locate[text] === "function" ? locate[text](param) : locate[text]
}