import constant from './index'
import app from '../../connection/Api'
import { getCategories } from './UserInformationAction'
import { callNotificationSnackbar } from './DashboardAction'
import Product from '../../views/products/class'
const product = new Product()

export const updateProductPriceList = (list) => {
	return { type : constant.UPDATE_PRODUCT_PRICE_LIST, list }
}
export const activeModalLoadMasiveProducts = (status) => {
	return { type : constant.ACTIVE_MODAL_LOAD_MASIVE_PRODUCTS, status }
}
export const calculatePricePerPercentage = (status) => {
	return { type : constant.CALCULATE_PRICE_PER_PERCENTAGE, status }
}
export const updateValueUseStock = (status) => {
	return { type : constant.UPDATE_VALUE_USE_STOCK, status }
}
export const updateProductForm = (attr, value) => {
	return { type : constant.UPDATE_PRODUCT_FORM, attr, value }
}
export const updateVariantList = (list) => {
	return {type : constant.UPDATE_VARIANT_LIST, list}
}
export const clearVariantList = () => {
	return {type : constant.CLEAR_VARIANT_LIST}
}
export const updateListsAfterSaving = (businessID) => (dispatch) => {
	try{
		app.get(`/get/all/products/${businessID}`)
		.then( res => {
			if(res.status === 1){
				if( res.products.length >= 1 ){
					dispatch({ type : 'LOAD_PRODUCTS', products : res.products })
					dispatch( getCategories(businessID) )
				}
			}
		})
	}catch(err){
		console.log(err)
	}
}
export const submitProductForm = (productForm, images) => async (dispatch) => {
	try{
		const data = product.validate(productForm)
		const result = await product.save(data, images)
		console.log(result)
	}catch(err){
		console.log(err)
	}
}

export const saveProducts = (products) => {
	return {type: constant.SAVE_PRODUCTS, products}
}

export const getProductList = () => async (dispatch) => {
	try{
		const result = await product.get()
		if( result.status ){
			dispatch(saveProducts(result.payload))
		}
	}catch(err){
		console.log(err)
	}
}